button{
  border-radius: 2px;
margin: 4px;
width: 150px;
border: none;
transition: all 0.5s;
cursor: pointer;
margin: 5px;
color: white;
font-Size: 12px;
}

button span {
cursor: pointer;
display: inline-block;
position: relative;
transition: 0.5s;
}

button span:after {
content: '\00bb';
position: absolute;
opacity: 0;
top: 0;
right: -20px;
transition: 0.5s;
}

button:hover span {
padding-right: 25px;
}

button:hover span:after {
opacity: 1;
right: 0;
}

